import styled from 'styled-components';

export const Container = styled.header`
    height: 100px;
    background-color: var(--backColor);
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;

    .title{
        text-transform:uppercase;
        font-weight:bolder;
        font-size:40px;
        letter-spacing:10px;
        margin:0px auto;
        /* text-shadow:2px 2px var(--backColor), 4px 4px var(--primaryColor), 5px 5px var(--primaryColor), 6px 6px var(--primaryColor); */
    }
    .subTitle{
        position:relative;
        text-transform:uppercase;
        font-size:12px;
        left:9.5px;
        top:-18px;
        margin:0px auto;
        letter-spacing:35px;
        color:var(--backColor2);
        font-weight:100;

    }

`;