import styled from 'styled-components';

export const Container = styled.header`
    height: 100%;
    max-width:100%;
    margin:0px auto;
    display:flex;
    justify-content:center;
    flex-wrap: wrap;

`;

export const CardBox = styled.div`
    min-height: 350px;
    width: 250px;
    margin: 10px 3vw;
    text-align:center;
    background-color:var(--backColor);
    border-radius:5px;
    display:flex;
    flex-direction:column;
    position:relative;

    *{
        width:100%;
    }
    div{
        margin:5px auto;
    }
    .price{
        img{
            width:15px;
            margin-right:5px;
        }
    }
    .del{
        margin-top:10px;
        max-width: 250px;
        background-color:var(--backColor2);
        height:30px;
        bottom:0px;
        border-radius:0px 0px 5px 5px;
        position:absolute;
        bottom:0px
    }
    .del:hover{
        background-color:#FB8C00;
    }

`;

export const CardAdd = styled.button`
    min-height: 350px;
    width: 250px;
    margin: 10px 3vh;
    text-align:center;
    background-color:var(--backColor);
    border-radius:5px;
    position:relative;

    .more{
        min-height: 350px;
        width: 250px;
        text-align:center;
        background-color:var(--backColor);
        border-radius:5px;
        display:flex;
        flex-direction:column;
        justify-content:center;
        align-items:center;
        font-size: 20px;
        color:white;
    }

    img{
        width:80px;
        margin:15px;
    }

`;

export const BoxAdd = styled.form`
    min-height: 350px;
    background-color:transparent;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    margin:0px auto;

    *{
        color:white;
        ::-webkit-input-placeholder{
            color:rgba(255,255,255,.5);
            
        }
        ::-moz-placeholder {
            color:rgba(255,255,255,.5);
        }
    }

    fieldset{

        border:2px solid var(--backColor2);
        -moz-border-radius:8px;
        -webkit-border-radius:8px;	
        border-radius:8px;
        min-height:30px;
        background-color:transparent;
        width:90%;
        margin:5px auto;

        input{
            min-height:20px;
            border:none;
            background-color:transparent;
            outline:none;
            padding:5px;
            width:100%;
        }

        select{
            min-height:20px;
            border:none;
            background-color:transparent;
            outline:none;
            padding:15px;
            width:100%;
            cursor: pointer;
            .option{
                font-size:15px;
            }
        }
        
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }
        input[type=number]{
            width:80%;
            -moz-appearance:textfield;
        }
        :focus-within {
            border-color: var(--primaryColor);
            :valid{
                border-color: var(--primaryColor);
            }
        }
        :required{
            border-color: crimson;
        }
        
        
        
    }
    .add{
        margin-top:10px;
        width: 250px;
        background-color:var(--backColor2);
        height:30px;
        bottom:0px;
        border-radius:0px 0px 5px 5px;
        position:absolute;
        bottom:0px;
        color:black;
    }
    .add:hover{
        background-color:#FB8C00;
    }
`;