import React,{Component} from 'react';
import {Container,CardBox,CardAdd,BoxAdd} from './styled';
import more from '../../Images/more.png';
import $ from 'jquery';

export default class Card extends Component {
    constructor(props){
        super(props);

        this.state={
            invisible: true,
            url: "",
            price: "",
            conta: "",
            farm:"",

        }

        this.card=JSON.parse(localStorage.getItem("dados"));

        console.log(JSON.parse(localStorage.getItem("dados")));

        this.remover = this.remover.bind(this);
        this.adicionar = this.adicionar.bind(this);
        this.mudarVisible = this.mudarVisible.bind(this);
        
    }

    mudarVisible(){
        this.setState({...this.state,invisible:!this.state.invisible})
    }

    remover(pos){
        let state = this.state;
        state.splice(pos, 1);
        this.setState(state);
    }

    adicionar(){
        let total = 60 * parseInt(this.state.price) / (parseInt(this.state.farm) * parseInt(this.state.conta));
        let m = Math.ceil(total);

        let h = Math.trunc(total/60);
        m -= (h * 60);

        let d = Math.trunc(h/24);
        h -= (d * 24);
       
        let novo ={
            img:this.state.url,
            price:this.state.price,
            d,
            h,
            m,
            quant:this.state.conta
        }

        let antes =[];
        antes.push(JSON.parse(localStorage.getItem("dados")));
        antes.unshift(novo);

        localStorage.setItem("dados",JSON.stringify(antes));
    }


    render(){
       

        return (
        <Container>
            
                {
                    this.card.length > 0 && this.card.map((item,i)=>{
                        return <CardBox key={i}>
                            <img alt="" src={item.img} />
                            <div className="price"><img alt="" src="https://johnpittertv.com/assets/img/icons/moedas.png"/>{item.price}</div>
                            <div className="time">{item.d} d e {item.h} h e {item.m} min</div>
                            <div className="quant">Farmando com {item.quant} contas</div>
                            <button className="del" onClick={()=>{this.remover(i)}}>Excluir esse sonho</button>
                        </CardBox>
                    })
                    
                }
            
            <CardAdd >
                { this.state.invisible ? 
                    <button className="more" onClick={()=>this.mudarVisible()}>
                        <img alt="" src={more} />
                        Adicionar sonho
                    </button>
                : 
                    <BoxAdd onSubmit={()=>{this.adicionar()}}>
                        <fieldset>
                            <legend>Imagem da Skin</legend>
                            <input type="url"  placeholder="URL da imagem" onChange={(e)=>this.setState({url:e.target.value})}></input>
                        </fieldset>
                        <fieldset>
                            <legend>Preço da Skin em pitterson</legend>
                            <input type="number" min="0" required  placeholder="Preço em pitterson" onChange={(e)=>this.setState({price:e.target.value})}></input>
                        </fieldset>
                        <fieldset>
                            <legend>Quantas contas são usadas</legend>
                            <input type="number" min="1" max="3" required  placeholder="Numero de contas farmando" onChange={(e)=>this.setState({conta:e.target.value})}></input>
                        </fieldset>
                        <fieldset>
                            <legend>Quanto está o Farm por hora</legend>
                            <input type="number" min="0" required  placeholder="Digite o Farm/h." onChange={(e)=>this.setState({farm:e.target.value})}></input>
                        </fieldset>
                        <button type="submit" className="add">Adicionar</button>
                    </BoxAdd>
                }
                
            </CardAdd>

        </Container>
        );
    }
}
